* {
  margin: 0
}

.app {
  background-color: #fafafa;
}

.app_header {
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: rgb(235, 235, 235);
  border-bottom: lightgray;
  display: flex;
  justify-content: space-between;
  padding: 20px;
  /* maintains aspect ratio of logo */
  object-fit: contain;
}

.app_headerImage {
  object-fit: contain;
}

.app_posts {
  padding: 20px;
  display: flex;
  justify-content: center;
}

.app_postsRight {
  margin-left: 20px;
}
.app_signup {
  display: flex;
  flex-direction: column;
}